* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-form {
  font: inherit;
  background-color: #8a2be2;
  justify-content: center;
  gap: 5px;
  margin-bottom: 16px;
  padding: 8px;
  display: flex;
}

.search-input, .search-btn {
  cursor: pointer;
  border: 2px solid #000;
  border-radius: 10px;
  font-size: 30px;
}

.search-input {
  padding-left: 10px;
}

.search-btn:hover, .search-btn:focus {
  color: #fff;
  background-color: #120f23;
}

.info {
  text-align: center;
  justify-content: center;
  display: flex;
}

.gallery {
  max-width: 1140px;
  grid-gap: 15px;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-auto-rows: 380px;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  display: grid;
}

.img-card {
  height: 240px;
  width: 100%;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
}

.photo-card {
  cursor: pointer;
  background-color: #f6eff7;
  border-radius: 0 0 4px 4px;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

.photo-card:hover, .photo-card:focus {
  transform: scale(1.03);
  box-shadow: 0 3px 1px rgba(0, 0, 0, .1), 0 1px 2px rgba(0, 0, 0, .08), 0 2px 2px rgba(0, 0, 0, .12);
}

